
import CascadeLoop from 'components/CascadeLoop'
import Scroller from 'vux/src/components/scroller/index.vue'

import { Popup,Picker  } from 'vant'
import {getEaring,getExpand} from 'api'
import {mapGetters} from 'vuex'
var month = new Date().getMonth();
var newMonthArray = [];
var monthArray=['1月榜单 01.01-01.31',
'2月榜单 02.01-02.28',
'3月榜单 03.01-03.31',
'4月榜单 04.01-04.30',
'5月榜单 05.01-05.31',
'6月榜单 06.01-06.30',
'7月榜单 07.01-07.31',
'8月榜单 08.01-08.31',
'9月榜单 09.01-09.30',
'10月榜单 10.01-10.31',
'11月榜单 11.01-11.30',
'12月榜单 12.01-12.31',
]
for (let i in monthArray) {
if (i < month) {
newMonthArray.push(monthArray[i])
}
}
export default {
name: 'rankpage',
components: {
CascadeLoop,
Scroller,
'van-popup':Popup,
'van-picker': Picker
},
data () {
return {
tabItem:['赚钱榜/TOP10','拓客榜/TOP10'],
clientWidth: document.body.clientWidth,
isActived: 0,
scrollHeight: '200px',
clientHeight: 0,
tabDefault: 1,
monthArray: newMonthArray,
thisMonth: '',
pickerModal: false,
space: []
}
},
computed:{
...mapGetters(['userInfo','identityCode'])
},
watch: {
clientWidth(val){
this.clientWidth = val
}
},
methods: {
getEaringList(data){
getEaring(data).then(res =>{
console.log(res)
})
},
getExpandList(data){
getExpand(data).then(res =>{
console.log(res)
})
},
handelTabbox(index){
this.isActived = index
this.tabDefault = index + 1
this.$refs.scrollerEvent.reset({top: 0})
},
onScroll (pos) {
this.scrollTop = pos.top
},
selectPicker(value){
this.pickerModal = false
this.thisMonth = value
}
},
mounted () {
const that = this;
that.$nextTick(() => {
window.onresize = () => {
return (() => {
window.screenWidth = document.body.clientWidth
window.screenHeight = document.body.screenHeight
that.clientWidth = window.screenWidth
that.clientHeight = window.screenHeight
})()
}
that.thisMonth = that.monthArray[month - 1]
that.scrollHeight = (that.clientHeight  - that.$refs.cascade.offsetHeight - 99) + 'px'
that.$refs.scrollerEvent.reset({top: 0})
})

}
}



